#about-me-container {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #4d936f;
  color: #e9e8e8; }
  #about-me-container h2 {
    font-family: "Raleway", serif;
    margin-top: 1vw;
    width: 70%;
    text-align: center;
    font-size: 3vw; }
    @media screen and (min-width: 1800px), screen and (orientation: portrait) {
      #about-me-container h2 {
        font-size: 5vw; } }
  #about-me-container .about-me-terminal {
    position: relative;
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
    font-size: 2vw;
    font-family: "Source Code Pro", monospace;
    padding-left: 20px;
    padding-right: 20px;
    border-top: 1.7vw solid #d1d0d0;
    background-color: #2f2f2f; }
    #about-me-container .about-me-terminal .about-me-terminal-button {
      position: absolute;
      top: -1.7vw;
      left: 5px;
      color: #ec7c75;
      font-size: 1.2vw; }
    #about-me-container .about-me-terminal div {
      margin-top: 1vw;
      margin-bottom: 1vw; }
    #about-me-container .about-me-terminal a {
      color: #e9e8e8; }



